import { Component, OnInit } from '@angular/core';

import { MoviedatabaseService } from '../moviedatabase.service';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css']
})
export class SearchComponent implements OnInit {

  results = [];
  showResults = false;

  popularMovies = [];

  constructor(private movieDatabaseService: MoviedatabaseService) { }

  searchSubmit = function(title: string){
    this.movieDatabaseService.getShowsByTitle(title).subscribe(data => {
      this.results = data.results;
      this.showResults = true;
      console.log(data.results);
    })
  }


  getPopularMovies = function(){
    this.movieDatabaseService.getPopularMovies().subscribe(data => {
      this.popularMovies = data.results;
      console.log(this.popularMovies);
    });
  }

  setShowDetails = function(show){
    this.movieDatabaseService.show = show;
  }

  ngOnInit() {
    // this.searchSubmit("game of thrones");
    this.getPopularMovies();
  
  }

}



