import { Component, OnInit, Input } from '@angular/core';

import { MoviedatabaseService } from '../../moviedatabase.service';

@Component({
  selector: 'app-results',
  templateUrl: './results.component.html',
  styleUrls: ['./results.component.css']
})
export class ResultsComponent implements OnInit {

  @Input() results = [];

  genres = this.movieDatabaseService.genres;

  constructor(private movieDatabaseService : MoviedatabaseService) { }

  setShowDetails = function(show){
    this.movieDatabaseService.show = show;
  }

  ngOnInit() {
  }

}



